import { useForm } from "react-hook-form";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";

const schema = yup.object().shape({
  name: yup.string().required("Please enter your name").min(3, "Required, minimum 3 characters"),
  lastName: yup.string().required("Please enter your last name").min(4, "Required, minimum 4 characters"),
  email: yup.string().required("Please enter an email address").email("Please enter a valid email address"),
  message: yup.string().required("Please enter your message").min(10, "The message must be at least 10 characters"),
});

export default function ContactForm() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(schema),
  });

  function onSubmit(data) {
    console.log(data);
  }

  console.log(errors);

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <label>Name</label>
      <input {...register("name")} />
      {errors.name && <span>{errors.name.message}</span>}

      <label>Last Name</label>
      <input {...register("lastName")} />
      {errors.lastName && <span>{errors.lastName.message}</span>}

      <label>Email</label>
      <input {...register("email")} />
      {errors.email && <span>{errors.email.message}</span>}

      <label>Subject</label>
      <select name="=Title" {...register("title")}>
        <option value="Mr">Customer Service</option>
        <option value="Mrs">Adminstration</option>
      </select>

      <label>Message</label>
      <textarea rows="7" {...register("message")} />
      {errors.message && <span>{errors.message.message}</span>}

      <button>Send</button>
    </form>
  );
}
