import { useState, useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";
import axios from "axios";
import Loading from "../common/Loading";

// this is the wordpress api i created and used for this project
// https://balawi.one/wp-json/

const API = process.env.REACT_APP_BASE_URL + "wp/v2/posts";

export default function AnimalDetails() {
  const [animal, setAnimal] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  let history = useHistory();
  const { id } = useParams();

  if (!id) {
    history.push("/");
  }
  const API_URL = API + "/" + id;

  useEffect(
    function () {
      async function getDetail() {
        try {
          const response = await axios.get(API_URL);
          console.log(response);
          setAnimal(response.data);
        } catch (error) {
          setError(error.toString());
        } finally {
          setLoading(false);
        }
      }
      getDetail();
    },
    [API_URL]
  );

  return (
    <>
      {loading ? (
        <Loading />
      ) : animal ? (
        <>
          <div className="detail-items">
            <h2 key={animal.id}>{animal.title.rendered}</h2>
            <img src={animal.featured_media_src_url} />
            <h3>{animal.slug}</h3>
            <p>{animal.yoast_head_json.og_description}</p>
          </div>
        </>
      ) : (
        setError(error)
      )}
    </>
  );
}
