import { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import FormError from "../common/FormError";
import Loading from "../common/Loading";

// this is the live wordpress api i created and used for this project
// https://balawi.one/wp-json/

const API = process.env.REACT_APP_BASE_URL + "wp/v2/posts?per_page=12";

export default function RenderAnimals() {
  const [animals, setAnimals] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(function () {
    async function getAnimals() {
      try {
        const response = await axios.get(API);
        setAnimals(response.data);
      } catch (error) {
        setError(error.toString());
      } finally {
        setLoading(false);
      }
    }

    getAnimals();
  }, []);

  if (loading) {
    return <Loading />;
  }

  if (error) {
    return <FormError>{error}</FormError>;
  }
  return (
    <>
      {animals.map((animal) => {
        return (
          <div key={animal.slug} className="wrapper">
            <Link to={`details/${animal.id}`}>
              <img className="animals-image" src={animal.featured_media_src_url} />
              <h2 className="animals-title">{animal.title.rendered}</h2>
            </Link>
          </div>
        );
      })}
    </>
  );
}
